n}\r\n\r\nmain {\r\n  flex: 1;\r\n   padding-bottom: 300px;\r\n}\r\n\r\n\r\nh1 {\r\n  text-align: center;\r\n  font-size: 2.5rem;\r\n  margin-bottom: 30px;\r\n  \r\n}\r\n\r\n h2 {\r\n        text-align: center;\r\n        margin-bottom: 10px;\r\n        font-size: 1.6rem;\r\n      }\r\n\r\n\r\n  /* Navbar */\r\n .navbar {\r\n    background: #4DA1FF;\r\n  } \r\n\r\n /* Colore dei link nella navbar */\r\n.navbar-light .navbar-nav .nav-link {\r\n  color: #FFD93D; \r\n}\r\n\r\n/* Link al passaggio del mouse */\r\n.navbar-light .navbar-nav .nav-link:hover {\r\n  color: #7CFCB8;\r\n}\r\n\r\n/* Link attivo o focus */\r\n.navbar-light .navbar-nav .nav-link:focus {\r\n  color: #7CFCB8;\r\n}\r\n\r\n/* Footer */\r\nfooter {\r\n  background-color:#4DA1FF; \r\n  color: white;\r\n}\r\n\r\nfooter h5 {\r\n   color: #FFD93D;             \r\n}\r\n\r\nfooter a {\r\n  color: white; \r\n   text-decoration: none; \r\n }\r\n\r\n\r\n/* Hover su link */\r\nfooter a:hover {\r\n  color: #7CFCB8;\r\n }\r\n\r\n/* Copyright */\r\nfooter .p-3 {\r\n  background-color: #063168;\r\n  color: white;\r\n  font-size: 0.85rem;\r\n}\r\n\r\n/*Pulsanti*/\r\n.btn {\r\nbackground:#FFD93D;\r\ncolor: #1269cc; \r\nborder: #000 solid 1px;\r\n}\r\n\r\n.btn:hover {\r\n  background:#4DA1FF;\r\n  color:#063168;\r\n  border: #000 solid 1px;\r\n}\r\n\r\n/* Scaffale */\r\n.shelf-section {\r\n  text-align: center;\r\n}\r\n\r\n\r\n.wood-shelf {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 0;\r\n  padding: 20px;\r\n  background: linear-gradient(#8b5a2b, #a0522d);\r\n  border: 8px solid #5c3a1a;\r\n  border-radius: 8px;\r\n  box-shadow: inset 0 -4px 0 #3e2611, 0 6px 12px rgba(0,0,0,0.2);\r\n  overflow-x: auto;\r\n  margin: 0 auto 40px auto; \r\n  width: 95%;\r\n}\r\n\r\n/* Libro */\r\n.book {\r\n  width: 32px;\r\n  height: 160px;\r\n  background-color: #c0392b;\r\n  border-left: 6px solid #2c3e50;\r\n  border-right: 3px solid #000;\r\n  border-top: 1px solid rgba(255,255,255,0.3);\r\n  border-bottom: 3px solid #000;\r\n  margin-right: 2px;\r\n  writing-mode: vertical-rl;\r\n  text-orientation: mixed;\r\n  color: white;\r\n  font-weight: bold;\r\n  font-size: 0.75rem;\r\n  text-align: center;\r\n  padding: 5px 2px;\r\n  box-shadow: 2px 3px 0 rgba(0,0,0,0.4);\r\n  cursor: pointer;\r\n  transition: transform 0.2s ease;\r\n  flex-shrink: 0;\r\n}\r\n\r\n/* Animazione libro */\r\n.book:hover {\r\n  transform: translateY(-5px) rotate(-2deg);\r\n}\r\n\r\n/* Colori libri */\r\n.book:nth-child(1)  { background-color: #e74c3c; }\r\n.book:nth-child(2)  { background-color: #3498db; }\r\n.book:nth-child(3)  { background-color: #f39c12; }\r\n.book:nth-child(4)  { background-color: #2ecc71; }\r\n.book:nth-child(5)  { background-color: #9b59b6; }\r\n.book:nth-child(6)  { background-color: #1abc9c; }\r\n.book:nth-child(7)  { background-color: #e67e22; }\r\n.book:nth-child(8)  { background-color: #34495e; }\r\n.book:nth-child(9)  { background-color: #d35400; }\r\n.book:nth-child(10) { background-color: #7f8c8d; }\r\n.book:nth-child(11) { background-color: #8e44ad; }\r\n.book:nth-child(12) { background-color: #16a085; }\r\n.book:nth-child(13) { background-color: #c0392b; }\r\n.book:nth-child(14) { background-color: #2980b9; }\r\n.book:nth-child(15) { background-color: #f1c40f; }\r\n.book:nth-child(16) { background-color: #e84393; }\r\n.book:nth-child(17) { background-color: #00cec9; }\r\n.book:nth-child(18) { background-color: #6c5ce7; }\r\n.book:nth-child(19) { background-color: #fd79a8; }\r\n.book:nth-child(20) { background-color: #fab1a0; }\r\n.book:nth-child(21) { background-color: #ff7675; }\r\n.book:nth-child(22) { background-color: #55efc4; }\r\n.book:nth-child(23) { background-color: #ffeaa7; }\r\n.book:nth-child(24) { background-color: #81ecec; }\r\n.book:nth-child(25) { background-color: #a29bfe; }\r\n.book:nth-child(26) { background-color: #d63031; }\r\n.book:nth-child(27) { background-color: #0984e3; }\r\n.book:nth-child(28) { background-color: #fdcb6e